import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import React, { useEffect, useState } from "react";
import Slider from "react-rangeslider";
import "react-rangeslider/lib/index.css";
import "../CSS/charts.css";
import { Doughnut } from "react-chartjs-2";
ChartJS.register(ArcElement, Tooltip, Legend);

function Charts() {
  const [loanAmount, setLoanAmount] = useState(1000);
  const [interestRate, setInterestRate] = useState(10);
  const [loanMonth, setLoanMonth] = useState(12);

  let r = interestRate / 100 / 12;
  let EMI =
    (loanAmount * r * Math.pow(1 + r, loanMonth)) /
    (Math.pow(1 + r, loanMonth) - 1);
  let totalInterest = (EMI * loanMonth - loanAmount).toFixed(2);
  let totalPayment = parseFloat(loanAmount) + parseFloat(totalInterest);
  let monthEMI = totalPayment / loanMonth;
  const interestPercentage = ((totalInterest / totalPayment) * 100).toFixed(2);
  const principlePercentage = ((loanAmount / totalPayment) * 100).toFixed(2);

  const [data, setData] = useState({
    labels: ["Principle Amount", "Interest Payable"],
    datasets: [
      {
        data: [loanAmount, totalInterest],
        backgroundColor: ["rgba(7, 232, 247, 0.8)", "rgba(66, 139, 237, 0.8)"],
        borderWidth: 0,
        borderJoinStyle: "miter",
        innerRadius: "99%",
        cutout: 80,
      },
    ],
  });
  useEffect(() => {
    setData({
      labels: ["Principle Amount", "Interest Payable"],
      datasets: [
        {
          data: [loanAmount, totalInterest],
          backgroundColor: [
            "rgba(7, 232, 247, 0.8)",
            "rgba(66, 139, 237, 0.8)",
          ],
          borderWidth: 0,
          borderJoinStyle: "miter",
          innerRadius: "99%",
          cutout: 80,
        },
      ],
    });
  }, [loanAmount, totalInterest]);

  function handleChangeAmount(loanAmount) {
    setLoanAmount(loanAmount);
  }
  function onChangeInterestRate(interestRate) {
    setInterestRate(interestRate);
  }
  function onChangeLoanMonth(loanMonth) {
    setLoanMonth(loanMonth);
  }
  return (
    <div>
      <div className="row">
        <div className="col-md-6">
          <div className="your-emi">
            <div className="emi-box">
              <div className="canvas">
                <div className="emi-text">
                  <p className="your-emi m-0">Your EMI is</p>
                  <h3 className="emi-value m-0">{"" + monthEMI.toFixed(2)}</h3>
                  <p className="your-emi m-0">Per Month</p>
                </div>
                <Doughnut data={data} height={300} />
              </div>
              <div className="d-flex justify-content-around mt-4">
                <div className="mx-3">
                  <p className="m-0">Principle Amount</p>
                  <div className="price">
                    {"" + loanAmount}{" "}
                    <span className="percentage">
                      {principlePercentage + "%"}
                    </span>
                  </div>
                </div>
                <div className="mx-3">
                  <p className="m-0">Interest Payable</p>
                  <div className="price">
                    {"" + totalInterest}{" "}
                    <span className="percentage">
                      {interestPercentage + "%"}
                    </span>
                  </div>
                </div>
              </div>
              <div className="total d-flex align-items-center flex-column">
                <p className="m-0 pt-3">Total Payment</p>
                <div className="price pb-4">{"" + totalPayment}</div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-6">
          <div className="rangeSlider-box">
            <div className="d-flex justify-content-between align-items-center loan-amount">
              <h6 className="m-0">Loan Amount</h6>
              <input
                value={loanAmount}
                maxLength="8"
                onKeyPress={(event) => {
                  if (!/[0-9]/.test(event.key)) {
                    event.preventDefault();
                  }
                }}
                className="form-control"
                onChange={(e) => setLoanAmount(e.target.value)}
              />
            </div>
            <div className="loan-amount-slider p-3">
              <Slider
                min={0}
                keyboardType="numeric"
                max={5000000}
                value={loanAmount}
                onChange={handleChangeAmount}
              />
            </div>
            <div className="d-flex justify-content-between align-items-center loan-amount">
              <h6 className="m-0">Interest Rate</h6>
              <input
                maxLength="3"
                value={interestRate + "%"}
                className="form-control"
                onChange={(e) => setInterestRate(e.target.value)}
              />
            </div>
            <div className="loan-amount-slider p-3">
              <Slider
                min={1}
                max={100}
                value={interestRate}
                onChange={onChangeInterestRate}
              />
            </div>
            <div className="d-flex justify-content-between align-items-center loan-amount">
              <h6 className="m-0">Loan Tenure(Months)</h6>
              <input
                maxLength="2"
                value={loanMonth}
                className="form-control"
                onChange={(e) => setLoanMonth(e.target.value)}
              />
            </div>
            <div className="loan-amount-slider p-3">
              <Slider
                min={1}
                max={100}
                value={loanMonth}
                onChange={onChangeLoanMonth}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Charts;
